<template>
    <pdd-layout
        page="shop-detail-layout"
        :bottom-text="bottomText">
        <div class="shop-detail-placeholder"/>
        <div class="shop-detail">
            <div class="shop-detail-info-placeholder"/>
            <div class="shop-detail-info">
                <img
                    class="shop-img"
                    :src='shopdetail.img_url'>
                <div class="shop-name">
                    {{ shopdetail.mallName }}
                </div>
                <span class="shop-act">内购专场</span>
                <span class="share-count">{{ shopdetail.salesTip }}</span>
            </div>

            <van-list
                v-model="loading"
                :finished="finished"
                @load="getShopDetail"
            >
                <div class="goods-list">
                    <div
                        v-for="(item,index) in shopdetail.list"
                        @click="goGoodsDetail(item.goodsId)"
                        :key='index'
                        class="good">
                        <img
                            v-lazy='item.imgUrl'
                            class="good-img">
                        <div class="good-name">
                            {{ item.name }}
                        </div>
                        <div class="good-info">
                            <span class="good-tag">拼单价</span>
                            <p class="unit">￥</p><p class="price">{{ item.computePrice }}</p>
                            <s
                                v-if='item.discount'
                                class="original-price">￥{{ item.originalPrice }}</s>
                        </div>
                        <div class="purchase-button">
                            立即抢购
                        </div>
                    </div>
                </div>
            </van-list>

        </div>
    </pdd-layout>
</template>

<script>
import { getShopDetail } from '@/api/pddMall';
import { get, set } from '@/utils/cache';
import PDDLayout from './components/PDDLayout';
import { computePrice, cacheShopInfo } from './config';


export default {
    name: 'PDDMallShopDetail',
    components: {
        pddLayout: PDDLayout,
    },
    created() {
        this.getShopInfo();
    },
    data() {
        return {
            shopdetail: {},
            page: 1,
            pageSize: 10,
            loading: false,
            finished: false,
            bottomText: '',
        };
    },
    methods: {
        getShopInfo() {
            let shop = this.$route.params.shop;
            if (!shop) {
                shop = JSON.parse(get(cacheShopInfo));
            }
            shop.list = [];
            this.shopdetail = shop;
            set(cacheShopInfo, JSON.stringify(this.shopdetail));
            this.getShopDetail();
            this.$root.hidePlaceholder();
        },
        getShopDetail() {
            this.loading = true;
            getShopDetail({
                mallId: this.shopdetail.mallId,
                page: this.page,
                pageSize: this.pageSize,
            }).then((data) => {
                this.page += 1;
                this.loading = false;
                if (!data.length) {
                    this.finished = true;
                    this.bottomText = '- 店铺全部优惠都在上面啦 -';
                }
                data.forEach((item) => {
                    // eslint-disable-next-line no-param-reassign
                    item.computePrice = this.computePrice(item.originalPrice, item.discount);
                });
                this.shopdetail.list = Object.freeze([...this.shopdetail.list, ...data]);
            }).catch(() => {
                this.$error();
            });
        },
        goGoodsDetail(goodsId) {
            this.$router.push({
                name: 'pddMallGoodsDetail',
                query: {
                    goodsId,
                },
            });
        },
        computePrice(originalPrice, discount) {
            return computePrice(originalPrice, discount);
        },
    },
};
</script>

