import { sset } from '@/utils/cache';

let context = null;
export const dialogSessionKey = 'pddmall-backdialog';

export const pddExchangeUrl = 'bikan://web.goto/commonWebView?url_encode=https%3A%2F%2Ffeed.browser.miui.com%2Fmobile-v2%2Fact%2FpddExchange';


export function computePrice(originalPrice, discount) {
    const result = originalPrice - discount;
    return Math.round(result) === result ? result : result.toFixed(1);
}

export function setContext(v) {
    context = v;
}

export function goBack(e) {
    if (e.state === 'backDialog' && context) {
        context.dialogShow = true;
        sset(dialogSessionKey, 'true');
        window.removeEventListener('popstate', goBack);
    }
}

