<template>
    <div class="layout">
        <div
            :class="['top', page]"
            :id='page'>
            <div
                class="back"
                @click="backHandler"
            />
            <slot name="top"/>
        </div>
        <div class="content">
            <slot />
        </div>
        <div :class="['bottom', page]">{{ bottomText }}</div>
    </div>
</template>

<script>
import { sget } from '@/utils/cache';
import { isXiangkan } from '@/utils/ua';
import { quitCurrentWebview } from '@/utils/jsApiV2/client';

const dialogSessionKey = 'pddmall-backdialog';

export default {
    props: {
        bottomText: {
            type: String,
            default: '',
        },
        page: {
            type: String, // goods,shops,channel
            default: 'channel',
        },
    },
    methods: {
        backHandler() {
            if (this.$route.name === 'pddMallChannel') {
                this.$o2o({
                    e_c: '首页返回',
                    e_a: '点击',
                    e_n: 'PDD商品列表页',
                });
            }
            // 如果本次会话已经提示过挽留弹窗
            if (isXiangkan() && sget(dialogSessionKey) === 'true'
                && this.$route.name === 'pddMallChannel') {
                quitCurrentWebview();
            } else {
                this.$router.back();
            }
        },
    },
};
</script>

